import Vue from 'vue'
import Router from 'vue-router'
import splash from '@/components/splash/splash'
import experience from '@/components/experience/experience'

Vue.use(Router)

export default new Router({
    mode: "history",
    routes: [
        {
            path: '/',
            name: 'Home',
            component: splash
        },
        {
            path: '/experience',
            name: "Experience",
            component: experience
        }
    ],
    scrollBehavior: function (to) {
        if (to.hash) {
            return {
                selector: to.hash
            }
        }
    },
})